/**** Grid ****/

.container {
  display: grid;
  grid-template-columns: [lft] 20px repeat(10, [col] 1fr) [rgt] 20px;
  grid-template-rows: repeat(5, [row] auto);
  grid-column-gap: 8px;
  grid-row-gap: 0px;
}
.leftcol {
  grid-column: col 1;
}
.full {
  grid-column: col 1 / span 10;
}
.margin {
  grid-column: lft;
}
.half {
  grid-column-end: span 5;
}
.small {
  grid-column-end: span 2;
}

section {
  margin-top: 2rem;
}
